import React, { Component } from "react";
import {
  CButton,
  CFormGroup,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CLabel,
  CInput,
  CInputFile,
  CSelect,
} from "@coreui/react";
import api from "../../api";
// import authHeader from "../../auth-header";
import authHeaderFile from "../../auth-header-file";
import Toast from "light-toast";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 43%;
  top: 50%;
  z-index: 1;
`;

class ServiceAdd extends Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.onChangeFile = this.onChangeFile.bind(this);
    this.serviceSubmit = this.serviceSubmit.bind(this);
    this.cancel = this.cancel.bind(this);

    this.state = {
      loading: false,
      icon: null,
      iconPreview: null,
      code: "",
      name: "",
      service: "",

      iconError: "",
      codeError: "",
      nameError: "",
      serviceError: "",
    };
    // this.reader = new FileReader();
  }

  handleChange = (input) => (e) => {
    console.log(input);
    console.log(e.target.value);
    this.setState({ [input]: e.target.value });
    this.setState({ [input + "Error"]: "" });
  };

  onChangeFile = (event) => {
    const imageFile = event.target.files[0];
    var stat = false;
    if (!imageFile) {
      this.setState({ iconError: "Please select image", icon: null });
      stat = true;
      //   return false;
    }
    // console.log(imageFile.name);
    if (!imageFile.name.match(/\.(jpg|jpeg|png)$/)) {
      this.setState({ iconError: "Please select valid image", icon: null });
      stat = true;
      //   return false;
    }

    if (Math.round(imageFile.size / 1024) > 300) {
      this.setState({ iconError: "Max file size 300kb", icon: null });
      stat = true;
      //   return false;
    }

    if (stat === false) {
      this.setState({
        icon: event.target.files[0],
        iconPreview: URL.createObjectURL(event.target.files[0]),
        iconError: "",
      });
    } else {
      console.log("failed");
      this.setState({
        icon: null,
        iconPreview: null,
      });
    }
  };

  serviceSubmit = () => {
    if (this.state.icon === null) {
      this.setState({ iconError: "Select Icon" });
    }

    if (this.state.code === "") {
      this.setState({ codeError: "Enter Code" });
    }
    if (this.state.code !== "") {
      if (this.state.code.length !== 3) {
        this.setState({ codeError: "Enter 3 char Code" });
      }
    }
    if (this.state.name === "") {
      this.setState({ nameError: "Enter Name" });
    }

    if (this.state.service === "" || this.state.service === "select") {
      this.setState({ serviceError: "Select Service Type" });
    }

    if (
      this.state.icon !== null &&
      this.state.code !== "" &&
      this.state.code.length === 3 &&
      this.state.name !== "" &&
      this.state.service !== "" &&
      this.state.service !== "select"
    ) {
      var formData = new FormData();
      formData.append("icon", this.state.icon);
      formData.append("code", this.state.code);
      formData.append("name", this.state.name);
      formData.append("serviceType", this.state.service);
      // formData.append("createdAt", new Date());
      // formData.append("updatedAt", new Date());

      this.setState({ loading: true });
      fetch(api.apiUrl + "add_service", {
        method: "POST",
        headers: authHeaderFile(),
        // body: JSON.stringify(data),
        body: formData,
      })
        .then((response) => response.json())
        .then((responseJsonFromServerData) => {
          // console.log(responseJsonFromServerData);
          if (responseJsonFromServerData === "success") {
            //   console.log("sucess1");
            Toast.success("Add successfully...", 600, () => {
              // do something after the toast disappears
              document.getElementById("serviceForm").reset();
              this.setState({ icon: null });
              this.props.history.push("/services");
            });
          } else if (responseJsonFromServerData === "failed") {
            Toast.fail("failed...", 600, () => {
              // do something after the toast disappears
            });
          } else if (responseJsonFromServerData === "format_invalid") {
            Toast.fail("Select Image...", 600, () => {
              // do something after the toast disappears
            });
          }
          this.setState({ loading: false });
        })
        .catch((error) => {
          console.error(error);
          this.setState({ loading: false });
        });
    }
  };

  cancel = () => {
    document.getElementById("serviceForm").reset();
    this.props.history.push("/services");
  };
  render() {
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <ScaleLoader
                height={40}
                width={12}
                css={override}
                size={150}
                //size={"150px"} this also works
                color={"#B54545"}
                loading={this.state.loading}
              />
              <CCardHeader>
                {" "}
                <CRow>
                  <CCol sm="6" xs="6" md="8">
                    <h3>Add Service</h3>
                  </CCol>
                  <CCol sm="6" xs="6" md="4"></CCol>
                </CRow>
              </CCardHeader>
              <CCardBody>
                <CRow>
                  <CCol md="3" sm="3" xs="1"></CCol>
                  <CCol md="6" sm="6" xs="10">
                    <form id="serviceForm">
                      <CFormGroup>
                        <CLabel htmlFor="">Icon</CLabel>
                        <CInputFile
                          id=""
                          name=""
                          onChange={this.onChangeFile}
                        />
                      </CFormGroup>
                      <div
                        style={{
                          marginBottom: "10px",
                          display: this.state.icon !== null ? "block" : "none",
                        }}
                      >
                        <img
                          style={{ maxWidth: "150px", maxHeight: "150px" }}
                          src={this.state.iconPreview}
                        />
                      </div>

                      <p
                        className="error"
                        style={{
                          display:
                            this.state.iconError === "" ? "none" : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.iconError}
                      </p>

                      <CFormGroup>
                        <CLabel htmlFor="code">Code</CLabel>

                        <CInput
                          autoFocus
                          type="text"
                          id="code"
                          name="code"
                          placeholder="eg. CCH"
                          onChange={this.handleChange("code")}
                          style={{
                            borderColor:
                              this.state.codeError === "" ? "" : "red",
                          }}
                          // style={{ textTransform: "uppercase" }}
                        />
                      </CFormGroup>
                      <p
                        className="error"
                        style={{
                          display:
                            this.state.codeError === "" ? "none" : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.codeError}
                      </p>

                      <CFormGroup>
                        <CLabel htmlFor="name">Name</CLabel>

                        <CInput
                          type="text"
                          id="name"
                          name="name"
                          placeholder="Name"
                          onChange={this.handleChange("name")}
                          style={{
                            borderColor:
                              this.state.nameError === "" ? "" : "red",
                          }}
                        />
                      </CFormGroup>
                      <p
                        className="error"
                        style={{
                          display:
                            this.state.nameError === "" ? "none" : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.nameError}
                      </p>

                      <CFormGroup>
                        <CLabel htmlFor="service">Service Type</CLabel>

                        <CSelect
                          custom
                          name="service"
                          id="service"
                          onChange={this.handleChange("service")}
                          style={{
                            borderColor:
                              this.state.serviceError === "" ? "" : "red",
                          }}
                        >
                          <option value="select">Select Service</option>
                          <option value="0">Garage</option>
                          <option value="1">Spares</option>
                          <option value="2">Ancillary</option>
                        </CSelect>
                      </CFormGroup>
                      <p
                        className="error"
                        style={{
                          display:
                            this.state.serviceError === "" ||
                            this.state.serviceError === "select"
                              ? "none"
                              : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.serviceError}
                      </p>
                      <CCol md="12" sm="12" xs="12" className="text-center">
                        <CButton
                          className="px-4 cancel-btn"
                          onClick={() => this.cancel()}
                        >
                          Cancel
                        </CButton>

                        <CButton
                          className="px-4 submit-btn"
                          onClick={this.serviceSubmit}
                        >
                          Submit
                        </CButton>
                      </CCol>
                    </form>
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}

export default ServiceAdd;
